
body {
  margin: 0;
  background: #f0efef;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Pokedex {
  text-align: center;
  margin: 20px auto;
}

.Pokedex-Winning {
  margin-top: 0;
  color: rgb(22, 172, 22);
}

.Pokedex-Losing {
  margin-top: 0;
  color: rgb(214, 33, 169);
}

.Pokedex-cards {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.Pokecard {
  background: #ffffff;
  width: 250px;
  text-align: center;
  border-radius: 3px;
  padding: 5px;
  display: inline-block;
  margin: 20px 50px;
  box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
  -webkit-animation: come-in 2s ease;
          animation: come-in 2s ease;
  box-sizing: border-box;
}

.Pokecard-img {
  display: flex;
  justify-content: center;
  justify-items: center;
  margin: 2px auto;
  width:  240px;
  height: 240px;
  background: #f0efef;
  border-radius: 3px;
}

.Pokecard-img img {
  -webkit-transition-duration: 2s, 2s;
          transition-duration: 2s, 2s;
}

.Pokecard-img img:hover {
  -webkit-transition-duration: 2s, 2s;
          transition-duration: 2s, 2s;
  -webkit-transform: scale(1.5,1.5);
          transform: scale(1.5,1.5);
  opacity: 0.9;
}

.Pokecard-data {
  font-size: .8rem;
}

.Pokecard-selected {
  box-shadow: 0px 0px 5px 5px rgba(209, 11, 11, 1.0);
}

@-webkit-keyframes come-in {
  from { -webkit-transform: translateY(-150%); transform: translateY(-150%) } to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes come-in {
  from { -webkit-transform: translateY(-150%); transform: translateY(-150%) } to { -webkit-transform: translateY(0); transform: translateY(0); }
}

